import React, { Component } from 'react';

class Reviews extends Component {
	render() {
		return (
			<div className="reviews container">
				<h4>Real Critic Reviews! Really!</h4>
				<p className="critic">
					<b>
						<i>FIVE STARS, HIGHEST RATING!</i>
					</b>
					<p />
					This is, without a doubt, the greatest piece of entertainment ever
					produced by sentient humans. This review is totally NOT being written
					by H.W. Zoozman Jr., because that would be wrong. Somebody completely
					different. A guy named, I dont know, Phil something. So there. Shut
					up.
					<p>- Not H.W. Zoozman Jr.</p>
				</p>
				<p className="critic">
					<b>
						<i>STILL WAITING TO BE PAID FOR THE ORIGINAL MOVIE.</i>
					</b>
					<p />
					Beefcake1933 here, the original Commander Brad Whiteman from the
					movie. Im 87 years old now, residing at Double-Wide Estates, a
					trailer park in Canoga Park. Its only $235 dollars fer gawdsakes. Im
					sick of dining on cat food canaps. Oh, and the guy who plays me in
					this version? Stinks.
					<p>- Niles LeBeefe</p>
				</p>
				<p className="critic">
					<b>
						<i>WHERES THE NUDITY?</i>
					</b>
					<p />
					Thought thered be nudity. Didnt someone promise nudity? I got to
					this thing from this Filipino website wherewell, its not important.
					Ill play it again, try a different interactive path. Maybe I just
					missed it.
					<p>- Joe</p>
				</p>
			</div>
		);
	}
}

export default Reviews;



